import { Plugin } from '@ali/4ever-cangjie';
import {
  LaserPointerHightPayload,
  LASER_POINTER_HIGHLIGHT,
  LASER_POINTER_REMOVE_HIGHLIGHT,
} from './actions';
import { highlightByPath, removeHighlight } from './utils';

const onAction: Plugin['onAction'] = (action, controller, next) => {
  const { type, payload } = action;

  if (type === LASER_POINTER_HIGHLIGHT) {
    const { path } = payload as LaserPointerHightPayload;
    highlightByPath(controller, path);
  } else if (type === LASER_POINTER_REMOVE_HIGHLIGHT) {
    removeHighlight(controller);
  }
  return next();
};

export default onAction;
